import styled from "styled-components";

import LinkButton from "../Button/LinkButton";
import { marginsCss } from "../styleguide";
import Typography from "../Typography/Typography";
import { pxToRem } from "../utils";

import { StyledInputProps } from "./Input.types";
import { sharedStylesWithTextArea, SharedWrapperStylesWithTextArea } from "./SharedWithTextArea.styles";

export type InputWrapperProps = Pick<StyledInputProps, "validation" | "disabled">;

export const ComponentWrapper = styled.div<StyledInputProps>`
    ${marginsCss}
`;

export const InputWrapper = styled.div<InputWrapperProps>`
  ${SharedWrapperStylesWithTextArea};
  padding: ${pxToRem(10)} ${pxToRem(12)};
`;

export const StyledInput = styled.input<StyledInputProps>`
    ${sharedStylesWithTextArea}
    padding-block: 0;
    // disable time picker menu so time is only selected via the keyboard
    ::-webkit-calendar-picker-indicator {
        display:none;
    }

    // remove up and down arrows added by browsers in number input
    &[type="number"] {
        -moz-appearance: textfield; // Firefox
    }
    &[type="number"]::-webkit-outer-spin-button,
    &[type="number"]::-webkit-inner-spin-button {
        -webkit-appearance: none; // Chrome, Safari, Edge
        margin: 0;
    }
	
`;

export const RightIconWrapper = styled.div<StyledInputProps>`
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    & > div {
        height: ${pxToRem(20)};
    }
`;

export const LeftIconWrapper = styled.div`
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
`;

export const Prefix = styled(Typography)<StyledInputProps>`
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: ${({ validation }) => validation === "invalid" ? 1 : 0.66};
`;

export const Suffix = styled(Typography)<StyledInputProps>`
    float: right;
`;

export const LinkButtonWrapper = styled.div`
    float: right;
`;

export const TrailingLinkButton = styled(LinkButton)`
    right: 12px;
    height: 100%;
    background: transparent;
`;